.about {

  &__container {
    padding: 105px 65px 50px 65px;
    background: #252525;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
    border-radius: 6px;

    @include small-tablet {
      padding: 90px 20px;
    }
  }

  &__title {
    text-align: center;
    margin-bottom: 60px;

    &::after {
      content: "about";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      font-weight: 700;
      font-size: 100px;
      line-height: 150px;
      letter-spacing: 0.2em;
      text-transform: uppercase;
      color: #303030;
      transform: translate(0px, -50px);
      z-index: -1;

      @include mobile {
        font-size: 60px;
        transform: translate(0px, -15px);
      }
    }
  }

  &__descr {
    margin: 0 auto;
    max-width: 734px;
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    text-align: center;
    color: var(--gray);
    margin-bottom: 90px;
  }


  &__content {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;

    @include small-tablet {
      flex-direction: column;
    }
  }

  &__slider {
    max-width: 734px;

    @include mobile {
      max-width: 100%;
    }

    .swiper-slide {
      height: 270px;

      img {
        height: 100%;
      }
    }
  }

  .controll__slider {
    background: #262626;
    border: 2px solid #303030;
    border-radius: 100%;
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: border-color 0.4s;
    cursor: pointer;
    padding: 15px;

    img {
      min-width: 15px;
    }

    &:hover {
      border-color: var(--white);
    }
  }

  .slider__next {
    transform: rotate(-180deg);
  }
}
