.hero {
  padding: 170px 0;
  background: linear-gradient(180deg, rgba(26, 26, 26, 0.0001) 0%, #1A1A1A 100%), linear-gradient(0deg, rgba(33, 33, 33, 0.5), rgba(33, 33, 33, 0.5)), url(../img/hero-bg.jpg) no-repeat left;
  background-size: cover;

  @include small-tablet {
    padding: 35px 0;
  }

  @include small-tablet {
    padding-top: 200px;
  }

  &__info {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    margin: 0;
    font-weight: 300;
    font-size: 72px;
    line-height: 84px;
    text-align: center;
    letter-spacing: 0.1em;
    text-transform: capitalize;
    color: var(--white);
    margin-bottom: 16px;

    @include small-tablet {
      font-size: 50px;
      line-height: 60px;
    }

    @include small-tablet {
      font-size: 30px;
    }
  }

  &__descr {
    margin: 0;
    max-width: 595px;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    text-align: center;
    letter-spacing: -0.2px;
    color: var(--white);
    mix-blend-mode: normal;
    opacity: 0.7;
    margin-bottom: 40px;
  }

  &__btn {
    padding: 25px 58px;
    background: var(--green);
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    letter-spacing: 0.2em;
    color: var(--black);
    border-radius: 43px;
    transition: 0.3s ease;

    &:hover {
      background: var(--white);
      color: var(--green);
    }
  }
}
