.dishes {
  @include small-tablet {
    padding: 35px 0;
  }

  &__title {
    text-align: center;
    margin-bottom: 90px;
    position: relative;

    &::after {
      content: "dishes";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      font-weight: 700;
      font-size: 100px;
      line-height: 150px;
      text-transform: uppercase;
      color: #303030;
      transform: translate(0px, -43px);
      z-index: -1;

      @include mobile {
        font-size: 60px;

      }
    }
  }

  &__row {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 30px;
  }

  &__col {
    flex: 0 1 350px;
    border-radius: 6px;
  }
}

.dishes-body {

  &__header {
    position: relative;

    img {
      vertical-align: bottom;
      border-radius: 8px 8px 0 0;
    }
  }

  &__decor {
    position: absolute;
    top: 24px;
    right: 24px;
    transition: 0.5s;

    &:hover {
      transform: scale(1.3);
    }
  }

  &__content {
    padding: 30px;
    background: #252525;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  }

  &__title {
    margin: 0;
    font-weight: 400;
    font-size: 32px;
    line-height: 30px;
    color: var(--light-gray);
  }

  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: var(--gray);
    margin-bottom: 20px;
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 13px;
    line-height: 18px;
    color: var(--gray);
    opacity: 0.8;
    margin-bottom: 28px;
  }

  &__footer {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include mobile {
      flex-direction: column;
      gap: 30px;
      align-items: flex-start;
    }
  }

  &__star {
    display: flex;
    align-items: center;
    gap: 11px;
  }

  &__star-num {
    margin: 0;
    font-weight: 700;
    font-size: 12px;
    line-height: 30px;
    color: var(--gray);
  }

  &__order {
    padding: 0 32px;
    font-weight: 700;
    font-size: 10px;
    line-height: 40px;
    text-align: center;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    background: #262626;
    border: 2px solid #303030;
    border-radius: 30px;
    transition: 0.3s;

    &:hover {
      background: #34C759;
      border-color: transparent;
    }
  }
}
