.footer {
  padding: 30px 0;
  background: #252525;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);

  .time {
    margin-top: 0;
    margin-bottom: 75px;
  }

  &__logo {
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 0;
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 12px;
    line-height: 60px;
    color: var(--gray);
    text-align: center;
  }
}
