.chefs {
  padding: 100px 0;

  @include small-tablet {
    padding: 35px 0;
  }

  &__title {
    margin: 0;
    font-weight: 400;
    font-size: 36px;
    line-height: 90px;
    text-align: center;
    letter-spacing: 0.2em;
    text-transform: capitalize;
    color: var(--light-gray);
    position: relative;
    margin-bottom: 60px;

    @include mobile {
      font-size: 30px;
      line-height: 60px;
    }

    &::after {
      content: "chefs";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      font-weight: 700;
      font-size: 85px;
      line-height: 150px;
      letter-spacing: 0.2em;
      text-transform: uppercase;
      color: #303030;
      transform: translate(0px, -27px);
      z-index: -1;

      @include mobile {
        font-size: 40px;
        transform: translate(0px, -15px);
      }
    }
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 30px;
  }

  &__col {
    flex: 0 1 350px;
  }

  &__descr {
    display: flex;
    align-items: center;
    gap: 24px;
    margin-bottom: 25px;
  }


  &__name {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    letter-spacing: -0.225px;
    color: var(--white);
    margin-bottom: 6px;
  }

  &__pos {
    margin: 0;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: -0.15px;
    color: rgba(255, 255, 255, 0.5);
  }

  &__menu {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;

    img {
      border-radius: 6px;
    }
  }
}
