.social {
  padding: 100px 0;

  @include small-tablet {
    padding: 35px 0;
  }



  &__tilte {
    text-align: center;
    margin-bottom: 90px;

    &::after {
      content: "social";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      font-weight: 700;
      font-size: 90px;
      line-height: 150px;
      letter-spacing: 0.2em;
      text-transform: uppercase;
      color: #303030;
      transform: translate(0px, -50px);
      z-index: -1;

      @include mobile {
        font-size: 60px;
        transform: translate(0px, -15px);
      }
    }
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
  }

  &__col {
    flex: 1 1 350px;
    background: #262626;
    border-radius: 6px;
  }



  &__col-platform {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #303030;
    padding: 25px;
    gap: 25px;

    @include mobile {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__col-icon {
    display: flex;
    align-items: center;
    gap: 20px;

    span {
      font-weight: 400;
      font-size: 14px;
      line-height: 30px;
      color: var(--gray);
    }
  }

  &__col-btn {
    padding: 0 18px;
    border: 2px solid #303030;
    border-radius: 30px;
    font-weight: 700;
    font-size: 10px;
    line-height: 40px;
    text-align: center;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    color: var(--gray);
    transition: .3s;

    &:hover {
      background: var(--green);
      color: var(--black);
    }

  }

  &__col-content {
    padding: 20px 10px 20px 25px;
  }

  &__col-data {
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    color: var(--light-gray);
  }

  &__col-text {
    margin-top: 10px;
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    color: rgba(255, 255, 255, 0.7);
    margin-bottom: 10px;
    height: 120px;
  }

  &__col-user {
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    color: var(--green);
  }

  &__col-gallery {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }

  &__col-gallery__item {
    height: 120px;
    position: relative;

    &:hover {
      a {
        opacity: 1;
      }
    }

    img {
      width: 100%;
      height: 100%;
      border-radius: 8px;
    }

    a {
      transition: .3s;
      position: absolute;
      top: 50%;
      left: 50%;
      width: 40px;
      height: 40px;
      transform: translate(-50%, -50%);
      opacity: 0;
    }
  }
}




.simplebar-content-wrapper {
  padding-right: 25px;
}

.simplebar-scrollbar:before {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 4px;
  height: 92px;
  background: var(--gray);
  border-radius: 100px;
  opacity: 1;
}
