.header {
  border-bottom: 1px solid rgb(255, 255, 255, 0.25);
  mix-blend-mode: normal;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include small-tablet {
      flex-direction: column;
      justify-content: center;
      padding: 0;
    }
  }
}



.nav {

  &__list {
    display: flex;
    gap: 45px;

    @include mobile {
      gap: 35px;
    }
  }

  &__item {
    padding: 0;
  }

  &__link {
    font-weight: 700;
    font-size: 14px;
    line-height: 90px;
    color: var(--white);
    transition: color 0.3s ease;

    &:hover {
      color: var(--green);
    }
  }
}

.nav-fixed {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: rgb(26, 26, 26, 0.5);

  .nav__link {
    line-height: 50px;
  }
}
