.time {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  margin-top: 70px;
  gap: 40px;

  &-item {
    flex: 1 0 340px;
    text-align: center;
  }

  &-icon {
    margin-bottom: 12px;
  }

  &-info {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    text-align: center;
    color: var(--white);
  }

  &-descr {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
    color: var(--white);
    mix-blend-mode: normal;
    opacity: 0.5;
  }

}
