.recipes {
  padding: 100px 0;

  @include small-tablet {
    padding: 35px 0;
  }

  &__title {
    text-align: center;
    margin-bottom: 90px;

    &::after {
      content: "recipes";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      font-weight: 700;
      font-size: 100px;
      line-height: 150px;
      letter-spacing: 0.2em;
      text-transform: uppercase;
      color: #303030;
      transform: translate(0px, -50px);
      z-index: -1;

      @include mobile {
        font-size: 40px;
        transform: translate(0px, -15px);
      }
    }
  }

  &__content {
    display: flex;
    justify-content: center;
    gap: 30px;

    @include small-tablet {
      flex-wrap: wrap;
    }
  }

  &__breakfast {
    flex: 0 1 540px;
    background: linear-gradient(180deg, rgba(38, 38, 38, 0.0001) 0%, rgba(38, 38, 38, 0.5) 100%), url(../img/breakfast.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 60px 20px 65px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 6px;
  }

  &__pos {
    display: inline-block;
    background: #000000;
    mix-blend-mode: normal;
    font-weight: 700;
    font-size: 10px;
    line-height: 25px;
    text-align: center;
    letter-spacing: 1.25px;
    text-transform: uppercase;
    color: var(--white);
    padding: 0 15px;
  }

  &__breakfast-data {
    text-align: center;
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    color: var(--white);
    margin-top: 60px;
    margin-bottom: 25px;
  }

  &__breakfast-info {
    max-width: 410px;
    margin: 0;
    font-weight: 400;
    font-size: 24px;
    line-height: 40px;
    text-align: center;
    color: var(--white);
    margin-bottom: 45px;
  }

  &__breakfast-cook {
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    color: var(--white);
    margin-bottom: 60px;
  }

  &__info {
    display: flex;
    gap: 32px;
    align-items: center;

    @include mobile {
      gap: 15px;
    }
  }

  &__info-views {
    display: flex;
    gap: 12px;
    align-items: center;

    span {
      font-weight: 700;
      font-size: 12px;
      line-height: 30px;
      color: var(--white);
    }
  }

  &__info-message {
    display: flex;
    gap: 12px;
    align-items: center;

    span {
      font-weight: 700;
      font-size: 12px;
      line-height: 30px;
      color: var(--white);
    }
  }
}

.menu {
  flex: 0 1 540px;
  display: flex;
  flex-direction: column;
  gap: 30px;
  justify-content: space-between;

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    color: var(--white);
    margin-bottom: 10px;
  }

  &__info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 25px;
  }

  &__descr {
    display: flex;
    align-items: center;
    gap: 15px;

    @include mobile {
      flex-direction: column;
    }
  }

  &__data {
    font-weight: 400;
    font-size: 12px;
    line-height: 25px;
    color: var(--gray);
  }
}

.lounch {
  background: #252525;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 6px;
  padding: 30px 25px 15px 30px;
}

.dinner {
  background: url(../img/dinner.jpg) center no-repeat;
  border-radius: 6px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  padding: 30px 25px 15px 30px;
  background-size: cover;
}

.sweets {
  background: #252525;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.05);
  border-radius: 6px;
  padding: 30px 25px 15px 30px;
  background-size: cover;

}
