.works {
  padding: 100px 0;

  @include small-tablet {
    padding: 35px 0;
  }

  &__title {
    text-align: center;
    margin-bottom: 60px;

    &::after {
      content: "works";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      font-weight: 700;
      font-size: 53px;
      line-height: 150px;
      letter-spacing: 0.2em;
      text-transform: uppercase;
      color: #303030;
      transform: translate(0px, -43px);
      z-index: -1;
    }
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 30px;
  }

  &__colum {
    flex: 1 1 255px;
    background: #252525;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
    border-radius: 6px;
    padding: 24px 24px 45px 24px;
    transition: 0.4s;

    &:hover {
      background: var(--green);

      .works__colum-title {
        color: var(--black);

        &::after {
          background: var(--black);
        }
      }

      .works__colum-text {
        color: var(--black);
      }
    }
  }

  &__colum-title {
    margin: 0;
    font-weight: 400;
    font-size: 22px;
    line-height: 26px;
    letter-spacing: -0.275px;
    text-transform: capitalize;
    color: var(--white);
    margin-bottom: 36px;
    position: relative;

    &::after {
      content: "";
      position: absolute;
      bottom: -15px;
      left: 0;
      width: 56px;
      height: 3px;
      background: var(--green);
    }
  }

  &__colum-text {
    margin: 0;
    font-weight: 400;
    font-size: 13px;
    line-height: 18px;
    letter-spacing: -0.1625px;
    color: var(--gray);
  }
}
