// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Helvetica", sans-serif;
  --content-width: 1110px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --white: #fff;
  --dark: #252525;
  --gray: #a6a6a6;
  --green: #34c759;
  --black: #1A1A1A;
  --light-gray: #D9D9D9;
}
